@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.all-info {
  background-color: #95a1a7;
  color: #fff;
  padding: 0.625rem;
}

.test-con {
  padding: 3.75rem 0;
}

.bg-gray {
  background-color: #dddddd;
}

.inner-over {
  margin: 0 calc(50% - 50vw);
  width: 100vw;
}

.c-bg-light-brown-right {
  background: linear-gradient(#f0e7e1 0%, #ded5cf 100%);
}
@media screen and (min-width: 768px) {
  .c-bg-light-brown-right {
    background: linear-gradient(to right, transparent 0%, transparent 0% 26%, #f0e7e1 0%, #ded5cf 100%);
  }
}

.c-bg-light-brown-right p,
.c-bg-light-brown-left p {
  height: 40vh;
}

.c-bg-light-brown-left {
  background: linear-gradient(#f0e7e1 0%, #ded5cf 100%);
}
@media screen and (min-width: 768px) {
  .c-bg-light-brown-left {
    background: linear-gradient(to left, transparent 0%, transparent 0% 26%, #f0e7e1 0%, #ded5cf 100%);
  }
}

.c-breadcrumbs {
  font-size: 0.75rem;
  margin-top: 0.5rem;
  margin-bottom: 2.5rem;
}
@media screen and (min-width: 768px) {
  .c-breadcrumbs {
    margin-bottom: 5rem;
    font-size: 0.875rem;
  }
}

.c-breadcrumbs a:not(:last-child) {
  transition: all 0.3s;
}

.c-breadcrumbs a:hover {
  text-decoration: underline;
}

.c-btn-slide {
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  padding: 0.5rem 1.5rem;
  align-content: center;
  color: #999;
  border: 1px solid #999;
  opacity: 1;
  position: relative;
  color: #023178;
  border-color: #023178;
}

.c-btn-slide::before {
  content: "";
  width: 0;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.25);
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.2s;
}

.c-btn-slide::after {
  content: "＞";
  font-size: 1.1em;
  position: absolute;
  right: 0.9em;
  top: 50%;
  transform: translateY(-50%);
  letter-spacing: 0;
  transition: 0.2s;
}

.c-btn-slide:hover::before {
  width: 100%;
}

.c-drower {
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
  transition: 0.3s;
  position: fixed;
  top: 0;
  left: 0;
  background-color: white;
  width: 100vw;
  z-index: 5;
  overflow: auto;
  height: 100vh;
  margin: 0 calc(50% - 50vw);
  width: 100vw;
}
@media screen and (min-width: 768px) {
  .c-drower {
    display: none;
  }
}

.c-drower__items {
  padding: 7.5rem 3.75rem 3.125rem;
}

.c-drower__item:not(:last-child) {
  padding-bottom: 2.5rem;
}
@media screen and (min-width: 768px) {
  .c-drower__item:not(:last-child) {
    padding-bottom: 3.75rem;
  }
}

.c-drower__item a {
  font-weight: 700;
  display: block;
  transition: all 0.3s;
  position: relative;
}

.c-drower__item a::before {
  content: "";
  background-image: url(../../assets/images/common/section-heading.svg);
  background-repeat: no-repeat;
  display: inline-block;
  background-size: contain;
  width: 14px;
  height: 14px;
}

.c-drower__item a:hover {
  text-decoration: underline red;
}

.c-drower.open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0%);
}

body.is-fixed {
  position: fixed;
  width: 100%;
  height: 100%;
}

.c-hamburger {
  display: block;
  position: relative;
  color: navy;
  z-index: 60;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger__bars {
  height: 1.875rem;
  margin-bottom: 0.1875rem;
}

.c-hamburger span {
  height: 0.0625rem;
  width: 1.4375rem;
  border-radius: 10px; /* 線幅の半分 */
  background-color: navy;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: all 0.3s;
}

.c-hamburger span:nth-of-type(1) {
  top: 25%;
}

.c-hamburger span:nth-of-type(2) {
  top: 40%;
}

.c-hamburger span:nth-of-type(3) {
  top: 55%;
}

.c-hamburger.active span:nth-of-type(1) {
  transform: rotate(45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
}

.c-hamburger.active span:nth-of-type(2) {
  transform: rotate(-45deg) translate(-50%, -50%);
  transform-origin: 0% 50%;
  top: 50%;
  left: 50%;
}

.c-hamburger.active span:nth-of-type(3) {
  opacity: 0;
}

.c-hamburger__menu {
  font-size: 0.625rem;
  white-space: nowrap;
  bottom: 0.1875rem;
  left: 50%;
  letter-spacing: 0;
}

