.p-archive-posts {
  gap: 40px 30px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
}
@media screen and (min-width: 768px) {
  .p-archive-posts {
    grid-template-columns: repeat(3, 1fr);
  }
}

.p-archive-post {
  display: block;
}

.p-archive-post__img {
  aspect-ratio: 387/238;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.p-archive-post {
  transition: all 0.3s;
}

.p-archive-post:hover {
  opacity: 0.7;
}

.p-archive-post__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.p-archive-post__time {
  color: #1958BC;
  margin-right: 10px;
}

.p-archive-post__meta {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.c-tag {
  border: solid 1px;
  background-color: #1958BC;
  color: #fff;
  padding: 4px 8px;
  border-radius: 10px;
  font-size: 12px;
  display: inline-block;
}

.p-archive-post__title {
  color: #1958BC;
  font-size: 1.25em;
  font-weight: bold;
  line-height: 1.35;
  margin-top: 10px;
}

.p-archive-post__desc {
  font-size: 1em;
  font-weight: normal;
  line-height: 1.3125;
  margin-top: 1em;
}

