@use 'global' as *;
.p-menu-wrap {
  position: relative;
  max-width: rem(400);
  width: 100%;
  margin: 0 auto;
  @include mq ('md'){
    max-width: rem(1050);
  
  }
}

.p-menu-slide {
  aspect-ratio: 4 / 3;
  max-width: rem(400);
  background: transparent;
  width: 100%;
  display: block;
  transition: all .3s;
}


.p-menu-slide__img {
  aspect-ratio: 3 / 2;
  max-width: rem(300);
  width: 100%;
}

.p-menu-slide__img img {
  transition: all .3s;
}

.p-menu-slide:hover .p-menu-slide__img img {
  transform: scale(1.1);
}


.p-menu-slide__name {
  background-color: red;
  color: white;
  position: absolute;
  top: 5%;
  left: 0;
  font-size: rem(18);
  line-height: 1;
  padding: rem(8) rem(10);
  z-index: 3;
}

.p-menu-slide__desc {
  color: white;
  position: absolute;
  writing-mode: vertical-rl;
  top: 0;
  right: 4%;
  font-size: rem(18);
  line-height: 1;
  padding: rem(8) rem(10);
  z-index: 3;
  text-shadow: 0px 0px 8px rgb(0 0 0 / 90%);
  text-align: left;
  line-height: 1.5;
}


.p-menu-swiper__button-next {
  right: -10px !important;
}
.p-menu-swiper__button-prev {
  left: -10px !important;
}